.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
  margin: 0;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-footer {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-top: auto;
}

table{
  margin-left: auto;
  margin-right: auto;
  border: 1px solid;
  border-collapse: collapse;
}
th, td {
  border: 1px solid;
  padding: 5px 20px;
}